}

.mobile-menu {
  opacity: 1;
  background-color: var(--mobile-menu-bg);
  width: 65%;
  height: 100%;
  color: var(--mobile-menu-text);
  z-index: 2;
  flex-direction: column;
  padding: 20px;
  transition: all .3s ease-out;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
  box-shadow: -8px 0 16px #00000026;
}

.mobile-menu.open {
  opacity: 1;
  right: 0;
}

.mobile-menu .close-btn {
  width: 32px;
  height: 32px;
  margin-left: auto;
  margin-right: 0;
  display: block;
}

.mobile-menu .close-btn-icon {
  stroke: currentColor;
}

.mobile-menu-nav {
  margin-top: 145px;
}

.mobile-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.mobile-menu .theme-switcher-btn {
  margin-top: auto;
  margin-bottom: 0;
  display: inline-block;
}

.mobile-menu-list-item {
  font-size: 12px;
  font-weight: 400;
}

.mobile-menu-list-item.mobile-menu-current {
  color: var(--mobile-menu-current);
  font-weight: 600;
}

body.dark-theme .mobile-menu {
  box-shadow: -8px 0 24px #9bb53740;
}

.theme-switcher {
  cursor: pointer;
  transition: all .3s ease-out;
  display: inline-block;
}

.theme-switcher__slider {
  background: #cecdcd;
  border-radius: 60px;
  width: 46px;
  height: 20px;
  transition: background-color .3s ease-out, box-shadow .3s ease-out;
  display: block;
  position: relative;
  box-shadow: 7px 5px 15px #a09ea099;
}

.theme-switcher__slider:before {
  content: "";
  background: #f8f8f8;
  border-radius: 18px;
  width: 18px;
  height: 18px;
  transition: all .3s ease-out;
  position: absolute;
  top: 1px;
  left: 1px;
}

.theme-switcher__input:checked + .theme-switcher__slider {
  background: #9bb537;
}

.theme-switcher__input:checked + .theme-switcher__slider:before {
  left: calc(100% - 1px);
  transform: translate(-100%);
}

.theme-switcher__input:checked + .theme-switcher__slider {
  box-shadow: 7px 5px 15px #16161699;
}

.theme-switcher__slider:active:before {
  width: 25px;
}

.theme-switcher__input:focus-visible + .theme-switcher__slider {
  outline-offset: 2px;
  border-radius: 50px;
  outline: 2px solid #1976d2;
}

@media (width <= 767px) {
  .theme-switcher {
    z-index: 3;
    position: fixed;
    bottom: 20px;
    right: -100%;
  }

  header.menu-opened .theme-switcher {
    right: calc(65% - 66px);
  }
}

.modal-close-btn {
